@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
html {
    --blue-color: #0d6efd;
    --danger-color: #dc3545;
    --success-color: #198754;
}

#admin-home {
    background-color: var(--white-color);
    padding: 50px;
    margin-top: 50px;
}

#admin-home>* {
    margin-bottom: 50px;
}

.action-btn {
    outline: none;
    border-radius: 3px;
    color: white;
    padding: 5px 15px;
    margin: 5px;
    cursor: pointer;
}

.action-btn-primary {
    background-color: #0d6efd;
    background-color: var(--blue-color);
    border: 1px solid #0d6efd;
    border: 1px solid var(--blue-color);
}

.action-btn-danger {
    background-color: #dc3545;
    background-color: var(--danger-color);
    border: 1px solid #dc3545;
    border: 1px solid var(--danger-color);
}

.action-btn-success {
    background-color: #198754;
    background-color: var(--success-color);
    border: 1px solid #198754;
    border: 1px solid var(--success-color);
}

#admin-menu {
    box-shadow: 0px 0px 5px var(--black-color);
    border-radius: 10px;
}

#admin-menu>header {
    background-color: var(--primary-color);
    color: var(--white-color);
    border-radius: 10px 10px 0px 0px;
    padding: 5px;
}

#admin-menu>nav>ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#admin-menu>nav>ul>li {
    padding: 5px;
    border-bottom: 1px var(--grey-color) solid;
    transition: 0.4s ease-in-out;
    cursor: pointer;
}

#admin-menu>nav>ul>li:last-child {
    border-bottom: 0px var(--grey-color) solid;
}

#admin-menu>nav>ul>li:hover {
    background-color: var(--secondary-color);
}

.menu-list {
    list-style: none;
}

#admin-info {
    padding: 5px;
}

.admin-list {
    border: 1px solid black;
}

.admin-list>header {
    background-color: var(--primary-color);
}

.ligne>* {
    border-bottom: 1px solid black;
    padding: 3px;
}

#admin-action>div>.action>form {
    padding: 5px;
    display: flex;
    margin-bottom: 15px;
    border: 1px solid black;
    text-align: right;
}

#admin-action>div>.action>form>*, #admin-action>div>.action>form>*>* {
    padding: 5px;
    margin: 5px;
}

#admin-action>div>.action>form>input, #admin-action>div>.action>form>*>input {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 1px solid black;
}

#article-img-add, #article-img-rename {
    width: 100% !important;
}

.command-card {
    text-align: left;
    border: 1px solid black;
    padding: 4px!important;
    word-break: break-word;
    background: rgb(255, 194, 199);
    background: linear-gradient(145deg, rgba(255, 194, 199, 1) 0%, rgba(235, 98, 151, 1) 100%);
    border-radius: 3px;
    box-shadow: 0px 0px 3px black;
    font-weight: bold;
    letter-spacing: 0.015rem;
}

@media screen and (max-width: 992px) {
    #admin-home {
        padding: 5px;
    }
}

#admin>div {
    min-height: 70vh;
}

#admin-form {
    border: 1px solid var(--primary-color);
    margin: 5px;
    padding: 15px;
}

#admin-form>* {
    padding: 10px;
}

#admin-form>input {
    padding: 10px;
    border: 1px solid var(--primary-color);
    margin: 15px 0px;
}
.client-card>div {
    padding: 5px;
    background-color: var(--grey-color);
    border: 1px solid var(--black-color);
    word-break: break-all;
    text-align: left;
}

.client-card {
    padding: 5px;
}

#delete-client {
    padding: 10px;
    border: 1px solid black;
}

#delete-client > input{
    padding: 5px;
    border: 1px solid black;
    margin: 10px;
}
.fr-btn {
  padding: 5px 25px;
  text-transform: uppercase;
  font-size: 1em;
  border: 2px solid var(--primary-color);
  outline: none;
  cursor: pointer;
  font-weight: bold;
  background-color: var(--primary-color);
  transition: 0.4s ease-in-out;
  color: var(--white-color);
}
.fr-btn:hover {
  background-color: transparent;
  color: var(--primary-color);
}
.title > h3{
  text-transform: uppercase;
  position: relative;
  z-index: 100;
  padding: 5px;
  margin-bottom: 15px;
  font-size: 1.7rem;
}
.title > h3:before{
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  left: 0%;
  bottom: 0%;
  background-color: var(--primary-color);
}
@media screen and (max-width: 992px) {
  .fr-btn {
    padding: 3px 15px;
    font-size: 0.9em;
  }
  .title > h3{
    font-size: 1rem;
  }
}

@font-face {
  font-family: "Lovelo";
  src: url("/static/media/Lovelo Black.6da216b7.otf") format("woff");
}

html {
  --primary-color: #EB6297;
  --secondary-color: #FFC2C7;
  --third-color: #fee3e5;
  --grey-color: #F2EFF2;
  --white-color: #FCFFFF;
  --black-color: #252525;
  background-color: #FCFFFF;
  background-color: var(--white-color);
}
::after,
::before,
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  position: relative;
  max-width: 100vw;
  min-height: 100vh;
  background-color: #FCFFFF;
  background-color: var(--white-color);
  color: #252525;
  color: var(--black-color);
  font-family: "Open Sans", sans-serif;
  text-align: center;
  overflow-x: hidden;
}
a,
a:hover {
  text-decoration: none;
  color: #FCFFFF;
  color: var(--white-color);
  transition: 0.3s ease-in-out;
}
a:hover {
  color: #252525;
  color: var(--black-color);
}
img {
  max-width: 100%;
}
input {
  outline: none;
  border: none;
}
nav {
  margin: 0px;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: "Open Sans";
}
/* width */
::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(255, 255, 255, 0);
}
/* Track */
::-webkit-scrollbar-track {
  background-color: #EB6297;
  background-color: var(--primary-color);
  border-left: 1px solid #EB6297;
  border-left: 1px solid var(--primary-color);
  border-right: 1px solid #EB6297;
  border-right: 1px solid var(--primary-color);
}
/* Handle */
::-webkit-scrollbar-thumb {
  background-color: #252525;
  background-color: var(--black-color);
  border-radius: 205px;
  cursor: pointer;
}

